@font-face {
  font-family: "Somar-Sans";
  src: url("./Public/fonts/Expanded/SomarSans-LightExpanded.otf") format("opentype");
}
@font-face {
  font-family: "Somar-Sans-Standard";
  src: url("./Public/fonts/Standard/SomarSans-Light.otf") format("opentype");
}
body {
  margin: 0;
  color: #000;
  font-family: "Somar-Sans", sans-serif;
  overflow-x: hidden;
}

/***************** Scroll behaviour ***********/
body::-webkit-scrollbar {
  width: 0;
  display: none;
}

/**************** scroller style ********************/
.scrollbar {
  position: fixed;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 999;
}

.dot {
  background-color: #ccc;
  border-radius: 12px;
  cursor: pointer;
  height: 20px;
  margin-bottom: 10px;
  transition: transform 0.3s ease-in-out;
  width: 10px;
}

.dot.active {
  transform: scale(1.5);
  margin-bottom: 1rem;
  border-radius: 5px;
  transform: scale(1.5);
  background: #fff;
  height: 2rem;
  transition: 0.3s all ease-in-out;
}

/***************** on scroll trigger navbar **************/
.responsive_logo {
  display: none;
}

.logo_2 {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out;
  right: 0;
}

.main-list {
  color: white;
}

.scroll-666 .logo {
  display: none;
  transition: 0.3s all ease-in-out;
}

.scroll-666 .logo_2 {
  position: relative;
  opacity: 1;
  visibility: visible;
}

.scroll-666 .main-list {
  color: black;
}

/****************** Hide on 1533 scroll and more *********/
